/* ################## Pijltjes ########################*/

.arrow{
  font-size: 4rem;
  display: -webkit-flex;
  display: flex;
  position: relative;
  z-index: 2;
}

.arrow:hover{
    cursor: pointer;
}

.prevArrow{
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    top: 20.5rem;
    padding-left: 2rem;
    width: 50%;
}

.nextArrow{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  bottom: 40rem;
  padding-right: 2rem;
  width: 50%;
  float: right;
}

