/*###################### CSS ART - HARTJE ######################################*/
.like{
    padding: 1rem;
    border-radius: 50%;
    z-index: 2;
    top: 6.5rem;
    left: 9rem;
    background-color: #90C2C4;
    background-color: var(--primary-color);
    border: .3rem solid #369093;
    border: .3rem solid var(--secondary-color);
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.like:hover{
    cursor: pointer;
}

.hartje{
    float: right;
    padding: .2rem;
    border: none;
    margin: 0.6rem;
    width: 2rem;
    height: 2rem;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    position: relative;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    border: none;
}

.hartje::before{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: white;
    border-radius: 50%;
}

.hartje::after{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: white;
    border-radius: 50%;
}

.hartje[data-liked=true], .hartje[data-liked=true]::before, .hartje[data-liked=true]::after {
    background-color: #CA4838;
    background-color: var(--tertiary-color);
}


.hartje--rood{
    float: right;
    padding: .5rem;
    border: none;
    margin: 0.3rem;
    width: 5px;
    height: 5px;
    background-color: red;
    display: -webkit-flex;
    display: flex;
    position: relative;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.hartje--rood::before{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: red;
    border-radius: 50%;

}

.hartje--rood::after{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: red;
    border-radius: 50%;
}

